@import url(https://fonts.googleapis.com/css?family=Kalam:400,700|Source+Sans+Pro:300,300i,400,700&display=swap);
/* 
$white: #fff
$black: #434f53
$dark-grey: #65787d
$light-grey: #3199b1
$font: 'Open Sans','Lato','sans serif'
$body-size: 18px
$body-text: #39424E */

.light-mode {
  background-color: white;
}

.dark-mode{
  background-color: #333333;
}

img{
  width: 100%;
  height: auto;
}

body {
  padding: 20px;
  margin: 0;
  background-color: white;
}

body,
td,
th,
tr,
p,
ul,
li,
a,
.content,
#content,
.header {
    font-family:'Source Sans Pro', 'Open Sans','Lato','Helvetica';
    font-size: 20px;
    font-weight:100;
    color: black;
  
}

.header li{
    list-style-type: none;  
    display: inline-block;
    padding-left: 2%;
    padding-right: 2%;
}

#contact li{
  list-style-type: none;  
  display: inline-block;
  padding-left: 2%;
  padding-right: 2%;
}

.content {
  padding: 0% 20%;
}

.header {
  font-family: 'Source Sans Pro','Open Sans','Lato','Helvetica';
  font-size: 25px;
  overflow: hidden;
  color: black;
  text-align: center;
}

#footer a{
  color: black;
  font-weight:bold;
  font-size: 22px;
}

#contact{
  font-size: 25px;
  text-align: center;
  margin: 4%;
}

#woo{
  font-family: 'Kalam';
  font-size: 25px;
  text-align: center;
  margin: 4%;
}

a {
  text-decoration: none;
  background-image: -webkit-linear-gradient(330deg, #d1aaf0  0%, #00b9d8 100%);
  background-image: linear-gradient(120deg, #d1aaf0  0%, #00b9d8 100%);
  background-repeat: no-repeat;
  background-size: 200% 0.2em;
  background-position: 0 90%;
  -webkit-transition: background-size 0.25s ease-in;
  transition: background-size 0.25s ease-in;
}

/* a:focus, */
a:hover {
  background-size: 100% 88%;
}

ul.header li a:focus,
ul.header li a:hover{
  color: black;
  text-decoration: none;
}

h1{
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-family: 'Kalam', 'Source Sans Pro','Helvetica', 'Lato', 'Open Sans';
  font-size: 5em;
  text-align: center;
  color: black;
}

h2 {
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-size: 2.0em;
  padding: 0% 0%;
  color: black;
}

h3,
h3 span,
h3 a{
  font-family: 'Source Sans Pro', 'Courier','Open Sans','Lato','Helvetica';
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-size: 22px;
  font-weight: bolder;
  color: #333;
}


#language {
  font-weight: bold;
}

@media (max-width: 700px) and (min-width: 480px) {

  h2{
    font-size: 25px;
  }

  h3,
  h3 span,
  h3 a{
    font-size: 22px;
  }

  body,
  tr,
  p,
  a, 
  li {
      font-size: 20px;
  }
  
  strong {
      font-size: 20px;
  }

  .content{
    padding: 0% 1%;
  }


  ul.header li {
    display: inline;
    list-style-type: none;
  }
  
  ul.header {
    font-weight:100;
  }
  
  ul.header li a {
    color: black;
    /* padding: 1%; */
    /* display: inline-block; */
  }
  #header{
    width: 95%;
  }

  #footer p {
      padding-top: 5%;
      padding-right: 2%;
  }

  #title {
      padding-top: 55px
  }

  .main {
      padding: 0% 2%;
  }

}

@media (max-width: 480px) {
  h1{
    font-size: 22px;
  }

  h2{
    font-size: 20px;
  }

  body,
  h3,
  h3 span,
  h3 a,
  tr,
  p,
  a,
  li {
      font-size: 18px;
  }

  ul a, ul, #header ul{
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    font-size: 15px;
  }

  .main ul{
    -webkit-padding-start: 5%;
            padding-inline-start: 5%;
  }

  #contact li{
    font-size: 10px;
    list-style-type: none;  
    display: inline-block;
    padding-left: 1%;
    padding-right: 1%;
  }
  
  strong {
      font-size: 16px;
      /* font-weight: 400; */

  }

  #footer p {
      padding-top: 5%;
      padding-right: 2%;
  }

  #title {
      padding-top: 55px
  }

  #header{
    width: 95%;
    text-align: center;
  }

  .main {
      padding: 0% 1%;
  }

  .content{
    padding: 10% 0.2%;
  }
}
